.container-testimony {
    width: 1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9% );

}

.img-testimony {
    height: 100%;
    width: 100%;
}

.container-texto-testimony {
    padding: 40px;
    font-family: Lato, sans-serif;
    font-weight: 400;
    line-height: 1.5rem;
}

.title-testomony {
    font-size: 1.3rem;
}

.job-testimony {
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}

